/**
 * 获取简历信息详情
 */
import axios from 'axios';
import {GET_RESUME_DATA} from '../mutation-types';
import { api, codeDic } from '../../../static/dicConfig';

const state = {
  resumeInfo: {}
}
const getters = {
  resumeInfo: state => state.resumeInfo
}
const actions = {
  getResumeInfoData({commit,state}, params,callback) {
    //请求获取数据http://svip.zhaopin.com/api/resumeOption/resumeDetails
    axios.get(`${api.host}/ResumeOption/resumeDetails`, {
      params: {
        CampusId: params.campusId,
        PostID: params.postID,
        RoomId: params.roomId,
      }
    }).then((response) => {
      if (response.status === 200) {
        if(response.data.returncode==='0')
        {
          //获取简历模版信息
          commit(GET_RESUME_DATA,response.data);
        }else{
          if(response.data.message){
            mui.toast(response.data.message);
          }
        }
        params.callback();
      }else{
        params.callback();
      }
    });
  }
}

const mutations = {
  [GET_RESUME_DATA](state, resumeInfo) {
    if (resumeInfo) {
      state.resumeInfo = resumeInfo
    }
  }
}

export default {
  state,
  actions,
  getters,
  mutations
}



