import axios from 'axios'
import {GET_EXAM_DATE} from '../mutation-types'
import { api, codeDic } from '../../../static/dicConfig'

const state = {
	examDate:[]
}
const getters = {
	examDate: state => state.examDate
}
const actions = {
	getExamDate({commit , state} , param){
		axios.get(`${api.host}${api.controllers.examRoom}/getExamDate`,{params:param}).then((res)=>{
			if (res.status === 200) {
				if (res.data.returncode === 0) {
					commit(GET_EXAM_DATE , (res.data.result||[]).map(val => val.split('-')[2]));
				}
			}
		});
	}
}
const mutations = {
	[GET_EXAM_DATE](state,examDate){
		state.examDate=examDate;
	}
}
export default {
	state,
	getters,
	actions,
	mutations
}


