import axios from 'axios'
import {GET_EXAM_DETAIL_LIST} from '../mutation-types'
import { api, codeDic } from '../../../static/dicConfig'

const state = {
	examDetailList:{data:{}}
}
const educationDicList = {
	'博士研究生': '博士',
	'硕士研究生': '硕士',
	'MBA-MBA': 'MBA',
	'大学本科': '本科',
	'大学专科': '专科',
	'高中': '高中',
	'初中': '初中',
	'其他':'其他',
	'中专/技校/职高': '中专类'

}
const getters = {
	examDetailList: state => state.examDetailList
}

const actions = {
	getExamDetailList({commit , state} , param){
		const {pageIndex , pageSize , roomId , signStatus, name , beforeCallBack , successCallBack , isClearCache , isSearch} = param;
		const params = {pageIndex , pageSize , roomId};
		let url=`${api.host}/examdetail/getExamDetail`;
		if (isSearch) {
			url=`${api.host}/examdetail/searchExamDetail`;
			params['name'] = name;
		}
		else{
			params['signStatus'] = signStatus;
		}
		beforeCallBack && beforeCallBack();
		axios.get(url , {params}).then((res) => {
			if (res.status === 200) {
				let resData=res.data;
				if (resData.returncode === 0) {
					res.data.result.isClearCache = isClearCache;
					commit(GET_EXAM_DETAIL_LIST , res.data.result);
					successCallBack && successCallBack(res.data.result);
				}
			}
		});
	}
}

const mutations = {
	[GET_EXAM_DETAIL_LIST](state , examDetailList){
		if(examDetailList.isClearCache){state.examDetailList.data = {};} 
		const cacheData = {};
		//根据时间段分租
		(examDetailList.data||[]).forEach((item)=>{
			cacheData[item.TimeRange] = cacheData[item.TimeRange] || [];
			//按对照表缩短学校类型
			item['Degree'] = educationDicList[item['Degree']];
			cacheData[item.TimeRange].push(item) 
		});
		state.examDetailList.noSign = examDetailList.noSign;
		state.examDetailList.total = examDetailList.total;
		
		Object.assign(state.examDetailList.data,cacheData);
	}
}

export default {
	state,
	getters,
	actions,
	mutations
}


