/**
 * 获取评论列表
 */
import axios from 'axios';
import {GET_COMMENT_DATA} from '../mutation-types';
import { api, codeDic } from '../../../static/dicConfig';

const state = {
  commentList: {}
}
const getters = {
    commentList: state => state.commentList
}
const actions = {
    getCommentDataList({commit,state}, params) {
    //请求获取数据http://svip.zhaopin.com/api/resumeOption/resumeDetails
    axios.get(`${api.host}/ResumeOption/getList`, {
      params: {
        CampusId: params.campusId,
        PostID: params.postId,
        RoomId: params.roomId,
      }
    }).then((response) => {
      if (response.status === 200) {
        if(response.data.returncode==='0')
        {
          //获取简历模版信息
          commit(GET_COMMENT_DATA,response.data);
        }else{
         if(response.data.message){
            mui.toast(response.data.message);
          }
        }
      }
    });
  }
}

const mutations = {
  [GET_COMMENT_DATA](state, commentData) {
    if (commentData) {
      state.commentList = commentData;
    }
  }
}

export default {
  state,
  actions,
  getters,
  mutations
}



