import axios from 'axios';
import { api, codeDic } from '../../../static/dicConfig';
import {
    SET_EXAM_ROOM_LIST,
    SET_EXAM_SEARCH_ROOM_LIST,
    SET_EXAM_CITY_LIST,
    SET_CURRENT_EXAM_CITY,
    CLEAR_EXAM_ROOM_LIST,
    CLEAR_EXAM_ROOM_SEARCH_LIST
} from '../mutation-types'
import { Store } from 'vuex';
import { resolve } from 'url';

let state = {
    examRoomList: [],
    examSearchRoomList: [],
    examCityList: { currentCity: { CityId: null, CityName: null }, dataList: [] },
}

let actions = {
    getExamRoomList({ dispatch, commit, state }, param = {}) {

        // axios.get(api.host + "examroom/getExamRoom", { params: param })
        return new Promise((resolve, reject) => {
            axios.get(`${api.host}${api.controllers.examRoom}/getExamRoom`, { params: param })
                .then((result) => {
                    if (result.status === 200 && result.data) {
                        let r = result.data;
                        if (r.returncode === 0) {
                            commit(SET_EXAM_ROOM_LIST, r.result);
                        }
                        resolve(r.result);
                    }

                }).catch(err => { reject(err) })
        })
    },
    getExamSearchRoomList({ dispatch, commit, state }, param) {
        // axios.get(api.host + "examroom/getExamRoom", { params: param })
        return new Promise((resolve, reject) => {
            axios.get(`${api.host}${api.controllers.examRoom}/getExamRoom`, { params: param })
                .then((result) => {
                    if (result.status === 200 && result.data) {
                        let r = result.data;
                        if (r.returncode === 0) {
                            commit(SET_EXAM_SEARCH_ROOM_LIST, r.result);
                        }
                        resolve(r);
                    }
                }).catch(err => {
                    reject(err);
                });
        })
    },
    getExamCityList({ commit, state }, param) {
        return new Promise((resolve, reject) => {
            // Object.assign(param, { _appid: 'rdiapi', rootCompanyId: 10890, userId: 928 })
            // axios.get(api.host + "examroom/getExamCity", { params: param })
            axios.get(`${api.host}${api.controllers.examRoom}/getExamCity`, { params: param })
                .then((result) => {
                    if (result.status === 200 && result.data) {
                        let r = result.data;
                        if (r.returncode === 0) {
                            let currentCity = r.result[0];
                            let cs = { dataList: r.result, currentCity: currentCity }
                            commit(SET_EXAM_CITY_LIST, cs);
                            console.log(1);
                            resolve();
                        }
                    }
                }).catch((err) => { reject(); })
        })
    }
    ,
    setCurrentExamCity({ commit, state }, param) {
        commit(SET_CURRENT_EXAM_CITY, param);
    },
    /**
     * 清除考场列表数据
     * @param {*} param0 
     * @param {*} param 
     */
    clearExamRoomList({ commit, state }, param) {
        commit(CLEAR_EXAM_ROOM_LIST, param);
    },
     /**
     * 清除考场搜索数据
     * @param {*} param0 
     * @param {*} param 
     */
    clearExamRoomSearchList({ commit, state }, param) {
        commit(CLEAR_EXAM_ROOM_SEARCH_LIST, param);
    }



}
let mutations = {
    [SET_EXAM_ROOM_LIST](state, param) {
        state.examRoomList = [...state.examRoomList, ...param];
    },
    [SET_EXAM_SEARCH_ROOM_LIST](state, param) {
        state.examSearchRoomList = [...state.examSearchRoomList, ...param];
    },

    [SET_EXAM_CITY_LIST](state, param) {
        state.examCityList.dataList = param.dataList;
        state.examCityList.currentCity = param.currentCity;
    },
    [SET_CURRENT_EXAM_CITY](state, param) {
        // state.examCityList.dataList = param.dataList;
        state.examCityList.currentCity = param.currentCity;
    },
    [CLEAR_EXAM_ROOM_LIST](state, param) {
        state.examRoomList = [];
    },
    [CLEAR_EXAM_ROOM_SEARCH_LIST](state, param) {
        state.examSearchRoomList = [];
    }
    
}
let getters = {
    examRoomList: state => state.examRoomList,
    examSearchRoomList: state => state.examSearchRoomList,
    examCityList: state => state.examCityList
}
export default {
    state,
    getters,
    actions,
    mutations
}


