import axios from 'axios';
import { api, codeDic } from '../../../static/dicConfig';
import {
  SET_USER_CENTER_INFO
} from '../mutation-types'
import { Store } from 'vuex';

let state = {
    userCenterInfo:{}
}
let actions = {
    getUserCenterInfo({ dispatch, commit, state }, param = {}) {
        return new Promise((resolve, reject) => {
            axios.get(`${api.host}/examdetail/getExamStatistics`, { params: param })
            .then((result) => {
                if (result.status === 200 && result.data) {
                    let r = result.data;
                    if (r.returncode === 0) {
                        commit(SET_USER_CENTER_INFO, r.result);
                    }
                    resolve();
                }

            }).catch(err=>{reject(err)})
        })
    },
    logOutRequest({ dispatch, commit, state }, param = {}) {
        return new Promise((resolve, reject) => {
            axios.post(`${api.svip}user/logout`,  param)
            .then((result) => {
                if (result.status === 200 && result.data) {
                    resolve();
                }

            }).catch(err=>{reject(err)})
        })
    }
}
let mutations = {
    [SET_USER_CENTER_INFO](state, param) {
        state.userCenterInfo =param;
    }
}
let getters = {
    userCenterInfo: state => state.userCenterInfo
}
export default {
    state,
    getters,
    actions,
    mutations
}


