import Vue from 'vue';
import Vuex from 'vuex';
import demo from './modules/demo';
import getResumeData from './modules/getResumeData';
import examRoom from './modules/examRoom';
import userCenter from './modules/userCenter';
import getCommentDataList from './modules/getCommentData';
import getExamDate from './modules/getExamDate'
import getExamDetailList from './modules/getExamDetailList'

Vue.use(Vuex);

export default new Vuex.Store({
    modules: {
        demo,
        getResumeData,
        examRoom,
        userCenter,
        getExamDate,
        getExamDetailList,
        getCommentDataList
    }
});



